.new-card-body{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    width: 100%;
    height: 100vh;
    background-color: rgba(0, 0, 0, 0.7);
}

.new-card-section{
    width: 73%;
    background-color: white;
    padding: 2rem 0;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}
.new-card-main{
    background-color: #edf3ff;
    padding: 2rem;
    width: 60%;
    display: block;
}

.new-card-main label{
    font-weight: 500;
    padding-top: 1rem;
    padding-bottom: .4rem;
}

.new-card-button{
    text-align: end;
}
.new-card-button button{
    border: #868fa0 1px solid;
    padding: .1rem 1rem;
    background-color: #edf3ff;
    font-weight: 600;
    border-radius: .3rem;
}

.new-card-number-input-div, .new-card-country-input-div{
    border: #868fa0 1px solid;
    width: 100%;
    display: flex;
    align-items: center;
    border-radius: .4rem;
    background-color: white;
}
.new-card-number-input-div input, .new-card-country-input-div input {
    border: none;
    outline: none;
    width: 92%;
    padding: .8rem;
    border-radius: .4rem;
    font-size: .8rem;

}

.new-card-expiration{
    display: block;
    width: 48%;
}
.new-card-expiration input, .new-card-CVC input{
    width: 100%;
    border: none;
    outline: none;
    padding: .8rem;
    border-radius: .4rem;
    font-size: .8rem;
}

.new-card-CVC{
    width: 48%;
}

.new-card-paragraph{
    width: 70%;
    font-size: 1.14rem;
    color: #676f7c;
    padding: 1rem 0;
}

.new-card-add-card-botton{
    text-align: center;
    padding-top: 1rem;
}
.new-card-add-card-botton button{
    width: 60%;
    background-color: #0267d3;
    padding: .6rem 0;
    border: none;
    color: white;
    font-size: 1.1rem;
    font-weight: 600;
    border-radius: .4rem;
}


/* MODAL */

.modal-content-added{
    background-color: white;
    width: 40%;
    text-align: start;
    padding: 2rem;
    border-radius: .5rem;
}
.pp{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}
.a1{
    margin-top: 5rem;
    margin-left: 10rem;
}

.modal-content-card-added button{
    background-color: #0267d3;
    width: fit-content;
    padding: 0.3rem 1.5rem;
}

.g3{
    margin-top: 1rem;
}