main{
    background-color: #f5f5f5;
    margin: 0;
    /* overflow: hidden; */
}

.section1{
    width: 100%;
}
.top-nav{
    display: flex;
    padding: 1rem;
    top: 0;
    position: sticky;
    z-index: 10;
}


.profile-img img{
    width: 2rem;
    border-radius: 100%;
    margin-left: .5rem;
}
.profile-name p{
    margin: 0;
    font-size: .65rem;
}
.profile-name div{
    font-size: .8rem;
    font-weight: 600;
}
.profile-name span{
    font-size: .5rem;
}
.profile-name{
    padding-left: .5rem;
}

.input-top-nav{
    border: 1px solid #868fa0;
    border-radius: 2rem;
    width: fit-content;
}

.input-top-nav input{
    border: none;
    outline: none;
    width: 25rem;
    color: #868fa0;
    padding: .5rem 0;
    font-size: .7rem;
    border-radius: 0 2rem 2rem 0;
}

.top-nav{
    background-color: white;
}
.input-top-nav div{
    padding: .5rem 1rem;
    color: #868fa0;
}

.right-top-nav-div .notification{
    position: relative;
    background-color: #f5f5f5;
    height: 2rem;
    width: 2rem;
    border-radius: 100%;
    margin-right: .5rem ;
}
.profile{
    border-left: 1px solid #868fa0;
}
.right-top-nav-div .notification div{
    position: absolute;
    top: 50%;
    left: 50%;
    color: #868fa0;
    transform: translate(-50%, -50%);
}
.right-top-nav-div .notification p{
    color: red;
    position: absolute;
    top: -38px;
    z-index: 1;
    right: 6px;
    font-size: 3rem;
    margin: 0;
}


.sub-logout p{
    font-size: .6rem;
    color: #868fa0;
}
.logout-side-nav{
    padding: 1rem;
}
.logout-icon div{
    background-color: #e3d1d1;
    color: rgba(255, 0, 0, 0.7);
    padding: 0 .4rem;
    border-radius: .2rem;
}

.sec1{
    font-size: .7rem;
}
.jj div{
    padding-left: .5rem;
    font-weight: 700;
}
.sec1 .col-3{
    border-radius: .3rem;
}
.trans p{
    font-size: .6rem;
    margin: 0;
}
.trans h4{
    font-weight: 700;
    color: rgba(0, 0, 0, 0.7);
}
.sec2-div1 .fImg img{
    width: 50%;
}
.sec2-div1 .fImg{
    text-align: end;
}
.sec2-div1 aside span{
    font-size: .7rem;
    padding-left: .1rem;
}
.sec2-div1 aside div{
    font-size: .8rem;
    font-weight: 500;
}
.sec2-div1 aside{
    display: flex;
    align-items: center;
}
.sec2-div1 .sub-sec2-div1{
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.sec2-div1{
    padding: .6rem;
    border-radius: .3rem;
}

.chart-sec{
    width: 60%;
    height: fit-content;
    background-color: white;
    padding-right: 1.3rem;
    padding-bottom: .3rem;
    border-radius: .5rem;
}
.div-top-of-barchart{
    border-bottom: 1px solid#DFE4EB;
    margin-bottom: 1rem;
    margin-left: 2rem;
    font-size: .8rem;
    padding: 1rem 0;
}
.div-top-of-barchart span{
    color: #868fa0;
    font-size: .7rem;
    padding-left: .7rem;
}
.div-top-of-barchart p{
    margin: 0;
    display: flex;
    align-items: center;
}

.sub-sec-beside-chart{
    background-color: white;
    width: 31%;
    height: 7.8rem;
    margin-bottom: .5rem;
    padding: 1rem;
    border-radius: .4rem;
}
.sub-sec-beside-chart p{
    font-size: .7rem;
    /* margin: 0; */
}
.sub-sec-beside-chart div{
    display: flex;
    align-items: center;
}

.sub-sec-beside-chart div h5{
    font-weight: 700;
}
.sec-beside-chart{
    width: 38%;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
}
.sub-sec-beside-chart img{
    width: 100%;
}


.body-pie-section{
    display: flex;
    /* justify-content: space-between; */
    align-items: center;
}
.pie-section{
    width: 49%;
    background-color: white;
}
.sub-body-pie-section div small{
    padding: 0 .4rem;
    margin: 0 1rem;
    border-radius: .2rem;
    font-size: .7rem;
}
.sub-body-pie-section div {
    margin: .6rem 0;
}
.sub-body-pie-section div span{
    font-size: .6rem;
    font-weight: 600;
}
.nav-pie-section{
    display: flex;
    justify-content: space-between;
}
.nav-pie-section div{
    font-size: .8rem;
    border: 1px solid #868fa0;
    color: #868fa0;
    display: flex;
    align-items: center;
    padding: 0 .4rem;
}
.nav-pie-section p{
    font-size: .9rem;
    color: #868fa0;
    font-weight: 600;
}
.footer-pie-section div{
    font-size: .8rem;
    font-weight: 600;
    color: #1b59f8;
    border-top: 1px solid rgba(134, 143, 160, 0.3);
    padding: .5rem 0;
}
.pie-section{
    padding: .8rem;
    border-radius: .4rem;
}
.head-body-footer div{
    font-size: .55rem;
    font-weight: 600;
}

.sub-body-footer{
    padding: 1rem 0;
    font-size: .6rem;
    font-weight: 750;
}
.sub-body-footer .with{
    color: #01b001;
}
.sub-body-footer .dep{
    color: #e01919;
}
.sub-body-footer .suc{
    color: #01b001;
    background-color: #e1f9e1;
    border-radius: .2rem;
    width: fit-content;
}
.sub-body-footer .failed{
    color: #e01919;
    background-color: #fde9ea;
    border-radius: .2rem;
    width: 4.7rem;
}
.nav-footer{
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: white;
    padding: .8rem 1rem;
    border-bottom: 1px solid rgba(134, 143, 160, 0.3);
    border-radius: .4rem .4rem 0 0;
}
.nav-footer div{
    font-size: .8rem;
    color: #1b59f8;
}
.body-footer{
    margin: 0 .78rem ;
}
.foot-footer{
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-size: .8rem;
    padding: 1rem;
    border-top: 1px solid rgba(134, 143, 160, 0.5);
}
.foot-footer p{
    margin: 0;
}
.sub-foot-footer{
    display: flex;
    justify-content: space-between;
    width: 30%;
}
.sub-foot-footer .previous{
    border: 1px solid rgba(134, 143, 160, 0.5);
    color: rgba(134, 143, 160, 0.5);
    padding: 0 .5rem;
}
.sub-foot-footer .n1{
    background-color: #1b59f8;
    color: white;
    padding: 0 .5rem;
}
.sub-foot-footer .n2{
    color: rgba(134, 143, 160, 0.5);
}
.sub-foot-footer .next{
    border: #1b59f8 solid 1px;
    color: #1b59f8;
    padding: 0 .5rem;
}