.sub-sidenav-section1 p{
    margin: 0;
    padding-left: .5rem;
}
.sub-sidenav-section1 div{
    font-size: 1.2rem;
    display: flex;
}
.sub-sidenav-section1{
    display: flex;
    align-items: center;
    justify-content: start;
    color: #868fa0;
    margin-bottom: .5rem;
    padding: .5rem 0;
    padding-left: .8rem;
    cursor: pointer;
    font-size: .8rem;
    font-weight: 600;
}
.skk{
    border-left: #1b59f8 5px solid;
    color: #1b59f8;
    background-color: rgba(27, 89, 248, 0.1);

}
.sub-sidenav-section1:hover{
    background-color: rgba(27, 89, 248, 0.1);
}
.sidenav{
    width: 18%;
    background-color: white;
    padding-top: 6rem;
    height: fit-content;
    position: sticky;
    top: 0;
}
.sidenav-section1{
    padding-bottom: 5rem;
    border-bottom: 1px solid #868fa0;
}


