body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.body--content{
  padding: 1.3rem;
}
.my-account-section{
  margin-bottom: 1rem;
}

.main-account{
  width: 49%;
  background-color: white;
  padding: 1rem;
  border-radius: .4rem;
}
.main-account-aside1{
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-weight: 500;
  color: rgb(0, 0, 0, 0.3);
}
.main-account-aside2{
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.sub2-main-account-aside2{
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.sub1-main-account-aside2 div{
  font-weight: 600;
  color: rgb(0, 0, 0, 0.5);
  font-size: .8rem;
}
.main-account-aside3{
  margin-top: 1rem;
  display: flex;
  justify-content: space-between;
}
.main-account-aside3-btn1{
  border: none;
  background-color: #e8eefe;
  color: #1b59f8;
  font-size: .8rem;
  font-weight: 600;
  padding: .5rem;
  border-radius: .3rem;
}
.main-account-aside3-btn2{
  border: none;
  background-color:#00b3fe;
  color: white;
  font-size: .8rem;
  font-weight: 600;
  padding: .5rem 1.2rem;
  border-radius: .3rem;
}

.total-onboard-agent{
  width: 49%;
  background-color: #e8eefe;
  padding: 1rem;
  color: #1b59f8;
  border-radius: .4rem;
}
.total-onboard-agent-aside2 span{
  color: green;
  font-weight: 500;
}
.total-onboard-agent-aside1{
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.total-onboard-agent-aside2{
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.sub1-total-onboard-agent-aside1 p{
  font-weight: 500;
}
.total-onboard-agent-aside1-btn{
  background-color: #1b59f8;
  color: white;
  border: none;
  font-size: .8rem;
  font-weight: 600;
  padding: .5rem 1.2rem;
  border-radius: .3rem;
}
.my-account-section{
  display: flex;
  justify-content: space-between;
}

.nav-footer2 button{
  background-color: #1b59f8;
  color: white;
  border: none;
  border-radius: .3rem;
  font-size: .8rem;
  padding: .6rem 1rem;
}
.nav-footer2 div{
  padding-left: 2rem;
  color: rgba(0, 0, 0, 0.3);
  font-weight: 500;
}
.sub-body-footer .toggle-on{
  color: green;
  font-size: 1.5rem;
}
.sub-body-footer .toggle-off{
  color: #868fa0;
  font-size: 1.5rem;
}

.sub-body-footer .concession span{
  background-color: #e6edfe;
  color: #1b59f8;
  font-size: .76rem;
  padding: .1rem .5rem;
  border-radius: .2rem;
}

.my-profile-aside2-img img{
  width: 4rem;
  border-radius: 50%;
}
.my-profile-aside2-img h5{
  padding-left: 1.5rem;
  font-weight: 700;
}
.my-profile-aside2-img{
  display: flex;
  align-items: center;
  padding: 2rem;
}
.my-profile-aside2{
  padding: 1rem 3.4rem;
  background-color: white;
}
.my-profile-aside3 label{
  font-weight: 600;
  padding: 1.5rem 0 .5rem 0;
}
.add-new-terminal-input1 input{
  width: 100%;
  padding: .9rem;
  border: 1px solid #868fa0;
  font-size: .7rem;
  border-radius: .3rem;
  margin-top: .5rem;
}
.add-new-terminal-input1 label{
  font-weight: 600;
}
.add-new-terminal-input1 {
  width: 48%;
  padding-top: 1rem;
}

.add-new-terminal-input1 button {
  margin-top: 1.9rem;
  padding: .75rem 0;
  width: 100%;
  border: none;
  background-color: #1b59f8;
  color: white;
  border-radius: .3rem;
  font-weight: 600;
}
.add-new-terminal{
  display: flex;
  justify-content: space-between;
}

.nav-footer2 .h{
  background-color: white;
  color: #868fa0;
  font-weight: 600;
}

.nav-footer2 .h:hover{
  background-color: #1b59f8;
  border: none;
  color: white;
}